.app{padding:10px}.box-wrapper{border:1px solid #e7e7e7;padding:10px;margin-bottom:10px}.title-box h4{margin-bottom:3px}.title-box p{margin-bottom:10px;font-size:13px;color:#555}.tabs-wrapper{border:1px solid #e7e7e7;display:inline-block}.tabs-wrapper .tabs-title{padding:0px;margin:0px;background:#e7e7e7}.tabs-wrapper .tabs-title li{list-style:none;display:inline-block;padding:5px 10px;cursor:pointer;transition:0.2s ease-in-out}.tabs-wrapper .tabs-title li:hover,.tabs-wrapper .tabs-title li.active{background:#ddd}.tabs-wrapper .tabs-content .tab-content{padding:10px}
